.dialogs {

    display: grid;

    grid-template-areas:
    "d m"
    ;

    grid-template-columns: 3fr 9fr;

    padding: 20px;
}

.dialogItems {
 grid-area: d;
}

.item a {
    color:violet;
    text-decoration: none;
}

.item img {
    height: 50px; 
    width: 50px;
    border-radius: 100px;
 }

.messages {
  grid-area: m
}

.message {
    color: blueviolet;
}

.message img {
    height: 10px;
    width: 10px;
    border-radius: 100px;
    margin-left: 5px;
}

.text {
    margin-top: 30px;
}